import { React, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Page from '../../../../layout/Page/Page';
import Button from '../../../../components/bootstrap/Button';
import Card, { CardBody, CardHeader } from '../../../../components/bootstrap/Card';
import { cirurgicalFlow } from '../../../../menu';
import Modal, { ModalBody, ModalFooter, ModalHeader, ModalTitle } from '../../../../components/bootstrap/Modal';

import Header from '../../../../components/Header';
import { DropdownItem, DropdownMenu, DropdownToggle } from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';


import "./style.css"
import Bandete from './bandeteIcon';
import User from './userIcon';
import ClockIcon from './clockIcon';

import API from '../../../../Services'



const PreparoLimpeza = () => {

	const navigate = useNavigate()
	
	const session = JSON.parse(localStorage.getItem('CAPP-local'))

	const localidadeHeader = `Sala ${ session.SUBLOCALIDADE === 'Hemodinamica' ? session.SUBLOCALIDADE : session.SUBLOCALIDADE.split(' ')[1]}`;


	const [paciente, setPaciente] = useState({})
	const [checklist, setChecklist] = useState([])
	const [possuiPacienteNaSala, setPossuiPacienteNaSala] = useState(false)
	const [ModalConfirmDesfazerEtapa, 	setModalConfirmDesfazerEtapa] 		= useState(false)


	async function identificaLocalidade() {
		try {
			const request    = await API.get(`/retornaEtapaPaciente/${session.CODIGO_SALA_CIRURGICA}`)
			const localidade = request.data[0];
			if(localidade.ETAPA != null){
				// eslint-disable-next-line default-case
				switch(localidade.ETAPA){
					case "LIMPEZA":
						navigate(`/salaCirurgica/limpeza`, {
							state: {
		 						MOTIVO_ALTA: localidade.CD_ETAPA,
								AVISO: 		 localidade.CD_AVISO_CIRURGIA,
								SAIDA_SALA:  false
							}
						}) 
					break;
					case "SALA CIRURGICA":
						navigate('/salaCirurgica/acoes')
					break;
				}
			
			}else{
				buscarPaciente()
				return;
			}
			
		} catch (error) {
			console.log(error)
		}
	}



	useEffect(() => {
		identificaLocalidade() 
		console.log(paciente)
	}, [])



	useEffect(() => {

		buscarPaciente()
		verificarAssinatura()

	}, [])




	const buscarPaciente = () => {

		const codigoSalaCirurgica = session.CODIGO_SALA_CIRURGICA
		API.get(`/retornaCirurgiasAgendadasPorSalaCir/${codigoSalaCirurgica}`)
			.then(response => {
				const { data } = response
				if (!data) {
					setPossuiPacienteNaSala(false)
					return;
				}

				setPossuiPacienteNaSala(true)
				setPaciente(data)
			})
	}

	const verificarAssinatura = () => {
		if (localStorage.getItem('0')) {
			navigate(`/salaCirurgica/acoes`)
			localStorage.removeItem('0')
		}
	}

	const irPara = () => {
		navigate(`/salaCirurgica/Listagem`)
	}

	const alterarLocalidade = () => {

		localStorage.removeItem('CAPP-local');
		navigate('/')

	};

	const receberPaciente = async () => {
		const param = paciente
		console.log('receberPaciente', param);
		console.log('session', session);
		/**
		 * falta identificar o resultado dessa api
		 */
		const RecuperaDadosProximaEtapa = await API.get(`/retornaProximaEtapa?cd_localidade=${session.CODIGO_LOCALIDADE}&cod_aviso=${param.COD_AVISO}`)
		console.log('receberPaciente', RecuperaDadosProximaEtapa);
		await setarCheckList(param)
		const info = {
			back: true,
			go: '/salaCirurgica/Recebimento',
			position: 0,
			//PROX_ETAPA: RecuperaDadosProximaEtapa.data[0].PROX_ETAPA
		}
		if (RecuperaDadosProximaEtapa) {
			goToPage(info)
		}
	}

	// eslint-disable-next-line no-unused-vars
	const goToPage = (info) => {
		navigate(`/salaCirurgica/AssinaturaRecebimento`, {
			state: {
				CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
				PROXIMA_ETAPA: 60,
				CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
				CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
				CODIGO_PROX_ETAPA: 60,
				...paciente
			}
		})
	}

	const setarCheckList = (pergunta) => {
		return new Promise((resolve) => {
			checklist.push(
				{
					'CdPergunta': 14,
					'Pergunta': '-',
					'Resposta': '-',
					'CodResposta': 9,
					'TpCampo': 'datetime',
					'CodAviso': pergunta.COD_AVISO,
					etapa: 'SALA_CIRURGICA'
				}
			)
			setChecklist(checklist)
			resolve()
		})
	}

	return (

		<PageWrapper title={cirurgicalFlow.SALA_CIRURGICA_RECEBIMENTO.text}>
			<Header titulo={localidadeHeader} localidade='Recebimento'>
				<Dropdown >
					<DropdownToggle>
						<Button color="light" icon="menu" isLight><p> </p></Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
						<DropdownItem onClick={()=> setModalConfirmDesfazerEtapa(true)}> 
							    Desfazer etapa do Paciente 
					    </DropdownItem>
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>


				<div className='row'>
					<div className="container" style={{ marginTop: '150px' }}>


						<div className="d-flex p-2 justify-content-center">

							{
								// Se houver um paciente nessa sala, mostra o botão de receber.
								possuiPacienteNaSala &&
								<Button onClick={receberPaciente} style={{ fontWeight: '600', position: 'relative', top: '-50px', fontSize: '20px', height: '60px' }} className="float-end" color="success" size="large"> Receber Paciente </Button>
							}
						</div>

						{
							// Se houver um paciente nessa sala, mostra o botão de receber.
							possuiPacienteNaSala ?


								<Card borderSize="0" borderColor="success" style={{ margin: 'auto' }}>
									<CardHeader style={{ backgroundColor: '#37BBAA', width: '100%' }}>
										<div style={{ textAlign: 'center', margin: 'auto', color: 'white', fontSize: '25px', fontWeight: 'bold' }}>
											{paciente.PACIENTE}
										</div>
									</CardHeader>
									<CardBody>

										<div className="col-12">
											<div className="row">
												<div className="col-6"
													style={{ margin: 'auto', textAlign: 'center' }}>
													<div className="row">
														<div className="col-12">
															<Bandete />
														</div>
														<div className="co-12" style={{ fontSize: '20px', marginTop: '5px' }}>
															{paciente.CIRURGIA}
														</div>
													</div>

												</div>
												<div className="col-3"
													style={{ margin: 'auto', textAlign: 'center' }}>
													<div className="row">
														<div className="col-12">
															<User />
														</div>
														<div className="co-12" style={{ fontSize: '20px', marginTop: '5px' }}>
															{paciente.NM_PRESTADOR}
														</div>
													</div>

												</div>
												<div className="col-3"
													style={{ margin: 'auto', textAlign: 'center' }}>
													<div className="row">
														<div className="col-12">
															<ClockIcon />
														</div>
														<div className="co-12" style={{ fontSize: '20px', marginTop: '5px' }}>
															{paciente.PREVISAO_INICIO}
														</div>
													</div>

												</div>
											</div>
										</div>
									</CardBody>
								</Card>
								:
								<center><h3 style={{ "color": "#BFC1C2" }}>Nenhum paciente foi encaminhado a esta sala.</h3></center>

						}

						<div className="row" style={{ marginTop: `65px` }}>

							<div className="d-flex p-2 justify-content-center">
								<Button
									onClick={irPara}
									style={
										{
											fontWeight: '600',
											fontSize: '20px',
											height: '60px',

										}
									}
									color="warning"
									size="large"
									className="align-self-center px-5">
									Buscar Paciente
								</Button>
							</div>
						</div>

					</div>

				</div>


				<Modal isCentered isOpen={ModalConfirmDesfazerEtapa} isAnimation={false} is>
						<ModalHeader style={{"backgroundColor":"#FECE64"}}>
							<ModalTitle style={{"color": "#fff"}}> 
								ALERTA
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h3>Você realmente confirma desfazer etapa do Paciente?</h3><br />
						</ModalBody>
						<ModalFooter>
							<Button className='btn-style' size='lg' color='light'  onClick={()=> {setModalConfirmDesfazerEtapa(false)}}>
								FECHAR
							</Button>
							<Button className='btn-style' size='lg' color='success'   onClick={()=> { 
								navigate('/salaCirurgica/AssinaturaDesfazerEtapa', {
									state: {
										DADOS: paciente
									}
								});
							}	
							}>
								CONFIRMAR
							</Button>
						</ModalFooter>
				</Modal>

			</Page>
		</PageWrapper>
	);


};

export default PreparoLimpeza;
